import * as running_config from "../config"
import axios from "axios"

const api_end_point = `${running_config.API_BASE}/storage/fixedPages/landingPage`;

export interface ISegment {
    title: string;
    details: string; 
}

export interface ILandingPage {
    segmentA: ISegment;
    segmentB: ISegment;
}

export const DefaultModel : ILandingPage = {
    segmentA : {
        title: "SegmentA title",
        details: "Segment A details"
    },
    
    segmentB: {
        title: "SegmentB title",
        details: "Segment B details"
    }
}

export const getData = async (token?: string|null)  => await axios.get<ILandingPage>(api_end_point); 

export const saveData: any = async (data: ILandingPage) => {
    
    const axiosConfig : any = {
        method: 'put',
        url: api_end_point,
        headers: {
            'Content-Type': 'application/json'
        },
        data : JSON.stringify({
            ...data,
            "id": "landingPage",
            "type": "fixedPages"
        })
    };

    await axios(axiosConfig);
}