import React from "react"
import {graphql} from "gatsby"
import { makeStyles, createStyles } from "@material-ui/core";
import {navigate} from "@reach/router";

import { HeroBanner } from "../components/HeroBanner";
import { InfoSection } from "../components/InfoSection";
import { VideoSection } from "../components/VideoSection";
import { PrepareForChangeSection } from "../components/PrepareForChangeSection";
import { ApplicationCardList } from "../components/ApplicationCardList";

import { useForm, usePlugin, useCMS, withPlugin  } from 'tinacms'

import { saveData, getData, ILandingPage } from "../cmsapi/index.cms";
import Layout from "../components/Layout";
import useIsInDraftMode from "../hooks/useIsInDraftMode";

import "./../global.css"
import {useToken} from "../hooks/useAuth";
import useIsInBrowser from "../hooks/useIsInBrowser";

export const useStyles = makeStyles((theme) =>
    createStyles({
      root: {
        flexGrow: 1,
      },
      contentContainer: {
        [theme.breakpoints.down("md")]: {
          paddingLeft: "32px",
          paddingRight: "32px",
        },
        [theme.breakpoints.down("sm")]: {
          paddingLeft: "16px",
          paddingRight: "16px",
        },
        [theme.breakpoints.down("xs")]: {
          paddingLeft: "8px",
          paddingRight: "8px",
        },
      },
    })
);

const ApplicationCreatorPlugin = {
  __type: 'content-creator',
  name: "New Application",
  fields: [
    {
      label: 'Id',
      name: 'id',
      component: 'text',
      validate(id: any) {
        if (!id) return "Required."
      }
    }
  ],
  onSubmit: async (formData:any) => await navigate(`app/applications/${formData.id}`),
}

const useGetFormConfig = (cms: any, data: ILandingPage) => {
  
  const loadInitialValues = async () => {
    let entry = data;
    if (useIsInDraftMode()) {
      const result = await getData();
      entry = result.data;
    }
    
    return entry;
  }

  return  {
    id: "landingPage",
    label: 'Edit Landing Page',
    fields: [
      {
        name: 'segmentA.title',
        label: 'Segment A Title',
        component: 'text',
      },
      {
        name: 'segmentA.details',
        label: 'Segment A Details',
        component: 'markdown',
      },
      {
        name: 'segmentB.title',
        label: 'Segment B Title',
        component: 'text',
      },
      {
        name: 'segmentB.details',
        label: 'Segment B Details',
        component: 'markdown',
      },
    ],
    loadInitialValues,
    onSubmit: async (formData: ILandingPage) => {
      const payload = {
        ...formData,
      }

      await saveData(payload)

      cms.alerts.success('Saved!')
    },
  }
  
}

interface LandingPageProps {
  data: {
    fixedPagesJson: ILandingPage
  }
}

const LandingPage = (props: LandingPageProps) => {

  const cms = useCMS();

  // @ts-ignore
  if (cms.plugins.getType(ApplicationCreatorPlugin) == null){
    cms.plugins.add(ApplicationCreatorPlugin)
  }

  const formConfig = useGetFormConfig(cms, props.data.fixedPagesJson);
  
  const [modifiedValues, form] = useForm(formConfig, { fields: formConfig.fields })

  usePlugin(form)
  
  return (
      <Layout banner={HeroBanner}>
        { modifiedValues.segmentA && <InfoSection title={modifiedValues.segmentA.title} description={modifiedValues.segmentA.details}/> }
        <VideoSection />
        { modifiedValues.segmentB && <PrepareForChangeSection title={modifiedValues.segmentB.title} description={modifiedValues.segmentB.details}/> }
        <ApplicationCardList />
      </Layout>
  );
};

export default withPlugin(LandingPage, ApplicationCreatorPlugin);

export const query = graphql`
  query {
    fixedPagesJson {
      segmentA {
        details
        title
      }
      segmentB {
        details
        title
      }
    }
  }
`
