import { Card, CardMedia, createStyles, makeStyles } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import heroAnim from "../assets/HeroAnim.mp4";
import heroImage from "../assets/HeroHolding.png";
import React, { useState } from "react";
import ReactPlayer from "react-player";

const useStyles = makeStyles((_theme) =>
	createStyles({
		voxelCardRow: {
			marginBottom: 24,
		},
		heroContainer: {
			backgroundColor: "#e7ebed",
			/*backgroundColor: "#b5b8b9",*/
		},
		heroBanner: {
			maxWidth: "1440px",
			display: "block",
			marginLeft: "auto",
			marginRight: "auto",
		},
	})
);

export const HeroBanner = () => {
	const classes = useStyles();
	const [
		hasAnimationPlaybackErrorOccurred,
		setAnimationPlaybackErrorOccurred,
	] = useState(false);

	const onReactPlayerAnimationError = () => {
		setAnimationPlaybackErrorOccurred(true);
	};

	return (
		<Grid className={`${classes.voxelCardRow}`} item xs={12}>
			<Card variant="outlined" className={classes.heroContainer}>
				{hasAnimationPlaybackErrorOccurred ? (
					<CardMedia
						className={classes.heroBanner}
						image={heroImage}
						width={1440}
						height="calc(80vmin - 120px)"
						component="img"
					/>
				) : (
					<ReactPlayer
						autoPlay
						loop
						muted
						playing={true}
						url={heroAnim}
						controls={false}
						width="100%"
						height="calc(80vmin - 120px)"
						onError={onReactPlayerAnimationError}
					/>
				)}
			</Card>
		</Grid>
	);
};
